import React from 'react'
import { useSelector } from 'react-redux'


const Notification = () => {

  const text = useSelector(state => state.notif)

  if (text === '') {
    return (
      <div className='empty-notif'>
        &nbsp;
      </div>
    )

  }
  return (
    <div className='notif'>
      <div className='exclamation'>!</div>
      {text}
    </div>

  )

}


export default Notification