import axios from 'axios'

const baseUrl = 'https://otsos-space-app.herokuapp.com/api/comments'

const getAll = async () => {
  const res = await axios.get(baseUrl)
  console.log('data', res.data)
  return res.data
}

const post = async (comment) => {
  const res = await axios.post(baseUrl, comment)
  return res.data
}

export default {
  getAll,
  post
}