import axios from 'axios'
const baseUrl = 'https://api.nasa.gov/planetary/apod?'
const keyString = 'api_key=ZLkfBiLh7H19ZeM6kNkEWnHa1MnqA6GmkbKK3pug'

const get = async () => {
  const res = await axios.get(`${baseUrl}${keyString}`)
  return res.data
}

const getFromDay = async (date) => {
  const res = await axios.get(`${baseUrl}date=${date}&${keyString}`)
  return res.data
}

export default {
  get,
  getFromDay
}