import searchService from '../services/search'

const initialState = {
  res: [],
  img: null
}

export const performSearch = (keyword) => {

  return async dispatch => {
    const res = await searchService.searchImg(keyword)
    dispatch({
      type: 'SEARCH_RESULTS',
      data: {
        res,
        img: null
      }
    })
  }
}

export const viewImage = (key) => {
  return async dispatch => {
    const imgArray = await searchService.getImg(key)
    console.log('imgArray: ', imgArray)
    const img = imgArray[0]
    dispatch({
      type: 'SET_IMG',
      data: img
    })

  }

}

export const initSearch = () => {
  return async dispatch => {
    dispatch({
      type: 'INIT_SEARCH',
      data: {
        res: [],
        img: null
      }
    })

  }

}

const searchReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'SEARCH_RESULTS':
      return action.data
    case 'INIT_SEARCH':
      return action.data
    case 'SET_IMG':
      return {
        ...state,
        img: action.data
      }
    default:
      return state
  }


}


export default searchReducer