import React from 'react'

const SearchResult = ({ findImg, result, styleName }) => {
  const imgFinder = () => {
    findImg(result.data[0].nasa_id)
  }
  //console.log('result: ', result)
  return (<div onClick={imgFinder} className={`${styleName} link`}>{result.data[0].title}</div>)
}


export default SearchResult