var timeoutId = null

export const setNotif = (text) => {
  //console.log(text)
  return async dispatch => {
    if (timeoutId !== null) {
      //console.log('clearing timeout')
      clearTimeout(timeoutId)
    }
    const timer = () => setTimeout(() => {
      dispatch(resetNotif())
    }, 5000)
    timeoutId = timer()
    dispatch({
      type: 'SET_NOTIF',
      data: text
    })
  }
}

export const resetNotif = () => {
  //console.log('reset!')
  return async dispatch => {
    dispatch({ type: 'RESET' })
  }
}

export const initializeNotif = () => {
  return async dispatch => {
    dispatch({ type: 'RESET' })
  }

}


const notifReducer = (state = '', action) => {
  switch (action.type) {
    case 'RESET':
      return ''
    case 'SET_NOTIF':
      return action.data
    default:
      return state

  }
}


export default notifReducer