import React, { useState } from 'react'
import Togglable from './Togglable'
import { useSelector, useDispatch } from 'react-redux'
import { viewDate, initializeApod } from '../reducers/apodReducer'
import Notification from './Notification'
import { setNotif, resetNotif } from '../reducers/notifReducer'
// eslint-disable-next-line react/prop-types
const ImageDesc = ({ imageDesc }) => {

  return (
    <div>
      {imageDesc}
    </div>
  )
}


const Apod = () => {
  const apod = useSelector(state => state.apod)

  const dispatch = useDispatch()
  const initApod = () => {
    dispatch(initializeApod())
  }

  const changeNotif = (text) => {
    dispatch(setNotif(text))
  }

  const removeNotif = () => {
    dispatch(resetNotif())
  }

  const searchDate = (event) => {
    event.preventDefault()
    const dateString = `${year}-${month}-${day}`
    console.log(dateString)
    dispatch(viewDate(dateString))
    setYear('')
    setMonth('')
    setDay('')
  }

  const [year, setYear] = useState('')
  const [month, setMonth] = useState('')
  const [day, setDay] = useState('')

  const handleYearChange = (event) => {
    const yearString = event.target.value
    if (!Number(yearString) && yearString !== '') {
      changeNotif('Give value as a number.')
    } else {
      (removeNotif())
    }
    setYear(event.target.value)
  }

  const handleMonthChange = (event) => {
    const monthString = event.target.value
    if (!Number(monthString) && monthString !== '') {
      changeNotif('Give value as a number.')
    } else {
      (removeNotif())
    }
    setMonth(monthString)
  }

  const handleDayChange = (event) => {

    const dayString = event.target.value
    if (!Number(dayString) && dayString !== '') {
      changeNotif('Give value as a number.')
    } else {
      (removeNotif())
    }
    setDay(dayString)
  }

  if (apod === undefined) {
    console.log(apod)

    return null
  }
  const copyright = (apod.copyright === undefined) ? 'Copyright: Public domain' : `(c) ${apod.copyright}`

  if (apod !== undefined) {
    return (
      <div className='content'>
        <h2>Astronomy Picture of the Day</h2>
        <h3>{apod.date}</h3>
        <div key={apod.url}>
          <div className='apodImg'>

            <img src={apod.url} alt={apod.title} />
            <br />
            <b>{copyright} </b>
            <br />
            <br />
          </div>
          <Togglable toggLabel='See description'>
            <b>Description:</b>
            <ImageDesc imageDesc={apod.explanation} />
          </Togglable> </div>
        <div className='previous-apod'>
          <b>View previous days&apos; picures</b>
          <br />
          <form onSubmit={searchDate}>
            <input id='year' value={year} onChange={handleYearChange} size='6' placeholder='YYYY' />
            <input id='month' value={month} onChange={handleMonthChange} size='3' placeholder='MM' />
            <input id='day' value={day} onChange={handleDayChange} size='3' placeholder='DD' />
            <br />
            <button type='submit'>Search</button> &nbsp; &nbsp; &nbsp;
            <button onClick={initApod}>Today&apos;s picture</button>
          </form>

          <div className='notif-div'>
            <Notification />
          </div>
        </div>
        <div className='external-link'>
          All material (c) Nasa.
          <br />
          <a href='https://apod.nasa.gov/apod/' target="_blank" rel="noopener noreferrer">APOD on Nasa homepage</a></div>
        <div>
        </div>
      </div>
    )
  }
}


export default Apod