import apodService from '../services/apod'


const initialState = {
  url: '',
  title: '',
  explanation: ''
}

export const initializeApod = () => {
  console.log('initializeApod called')
  return async dispatch => {
    const apod = await apodService.get()
    const toDispatch = {
      type: 'INIT_APOD',
      data: apod,
    }
    console.log(toDispatch)
    dispatch(toDispatch)
  }
}

export const viewDate = (date) => {
  console.log(`viewing date ${date}`)
  return async dispatch => {
    const apod = await apodService.getFromDay(date)
    const toDispatch = {
      type: 'VIEW_DATE',
      data: apod
    }
    dispatch(toDispatch)
  }
}

export const resetNotif = () => {
  return { type: 'NOLLAA' }
}

const apodReducer = (state = initialState, action) => {
  switch (action.type) {
    case 'INIT_APOD':
      return action.data
    case 'VIEW_DATE':
      return action.data
    default:
      return state
  }
}

export default apodReducer