import React, { useEffect, useState } from 'react'
import { useSelector } from 'react-redux'
import SearchForm from './SearchForm'
import SearchResult from './SearchResult'
import { initSearch, performSearch, viewImage } from '../reducers/searchReducer'
import { useDispatch } from 'react-redux'

const Search = () => {

  //const [imgToDisplay, setImgToDisplay] = useState('')

  const search = useSelector(state => state.search)
  const searchResult = search.res
  const img = search.img
  console.log('img: ', img)
  console.log('search: ', search)

  const dispatch = useDispatch()

  useEffect(() => {
    dispatch(initSearch())
  }, [])

  const searchCat = () => {
    dispatch(performSearch('cat'))
  }

  const resultClassNames = ['result-dark', 'result-bright']

  const findImg = async (key) => {

    dispatch(viewImage(key))
  }

  //const result = searchService.searchImg(keyword)
  if (searchResult !== [] && searchResult !== undefined) {
    //console.log('searchResult: ', searchResult)
    return (
      <div>
        <SearchForm />
        <br />
        <img src={img} className='search-img' />
        <h2>Search results:</h2>
        {searchResult.map((res, index) =>
          <SearchResult result={res} key={res.data[0].nasa_id} styleName={resultClassNames[index % 2]} findImg={findImg} />
        )}
        <br />
      </div>
    )
  }


  return (
    <div>
      Something went wrong. Sorry.
    </div>
  )
}

export default Search